import { Carousel, CarouselItem, Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { getMobileScaleWidth } from "../../../utils/layout-helper";

const useStyles = createUseStyles({
  container: {
    position: "relative",
    width: "67%",
    height: `${getMobileScaleWidth(375)}px`,
  },
  carousel: {
    position: "relative",
    height: "100%",
    "& .carousel-inner": {
      height: "100%",
      width: "90%",
      margin: "auto",
    },
    "& a": {
      opacity: "1 !important",
      width: "6%",
    },
  },
  bg: {
    position: "absolute",
    left: 0,
    right: 0,
    margin: "auto",
    width: "100%",
  },
  fish: {
    height: "100%",
    margin: "auto",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    "& img": {
      maxHeight: "65%",
      maxWidth: "70%",
      width: "auto",
      height: "auto",
    },
  },
});
export const CaCarousel = (props) => {
  const classes = useStyles();
  function NewArray(size) {
    var x = [];
    for (var i = 0; i < size; ++i) {
      x[i] = i;
    }
    return x;
  }
  const fishesIdx = NewArray(props.numFishes);

  return (
    <div className={classes.container}>
      <Image
        className={classes.bg}
        src={`images/he_thong_ca/${props.src}/frame.png`}
      ></Image>

      <Carousel
        prevIcon={
          <>
            <Image
              fluid
              className="ms-5 pt-2"
              src="images/he_thong_ca/left.png"
            ></Image>
          </>
        }
        nextIcon={
          <>
            <Image
              fluid
              className="me-5 pt-2"
              src="images/he_thong_ca/right.png"
            ></Image>
          </>
        }
        interval={null}
        className={classes.carousel}
        indicators={false}
        touch={true}
      >
        {fishesIdx.map((v, idx) => {
          return (
            <CarouselItem className="h-100" key={idx}>
              <div className={classes.fish}>
                <Image
                  fluid
                  className={classes.fishImage}
                  src={`images/he_thong_ca/${props.src}/${idx}.png`}
                ></Image>
              </div>
            </CarouselItem>
          );
        })}
      </Carousel>
    </div>
  );
};
