import { Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
const useStyles = createUseStyles({
  bannerContainer: {
    position: "relative",
    width: "100%",
  },
  bannerItem: {
    "& img": {
      borderRadius: "8px",
      width: "95%",
    },
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  bannerFrame: {
    borderRadius: "0",
    position: "absolute",
    width: "98%",
    top: "-2.5%",
    left: 0,
    composes: ["center"],
    overflow: "hidden"
  },
});
export const BannerItem = (props) => {
  const classes = useStyles();

  const { src } = props;
  return (
    <div className={classes.bannerContainer}>
      <div className={classes.bannerItem}>
        <Image fluid className={classes.bannerImage} src={src}></Image>
      </div>
      <Image
        onClick={(e) => {
          e.preventDefault();
        }}
        className={classes.bannerFrame + ` pointer-none`}
        fluid
        src="/images/gioi_thieu/frame_2.png"
      ></Image>
    </div>
  );
};
