import { useRef } from "react";
import { Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { getMobileScaleWidth } from "../../../utils/layout-helper";
import { BannerCarousel } from "./BannerCarousel";

const useStyles = createUseStyles({
  textGioiThieu: {
    position: "absolute",
    top: "14.3%",
    width: "70%",
    composes: ["center"],
  },
  videoGroup: {
    position: "absolute",
    top: "16.3%",
    width: "100%",
    height: `${getMobileScaleWidth(330)}px`,

    composes: ["center"],
  },
  videoPlay: {
    width: "97%",
    borderRadius: "16px",
  },
  frame: {
    position: "absolute",
    width: "80%",
  },
  video: {
    width: "80%",
    height: "100%",
    margin: "auto",
    overflow: "hidden",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
});
export const Introduction = () => {
  const videoRef = useRef();
  const classes = useStyles();

  document.addEventListener("scroll", (e) => {
    var doc = document.documentElement;
    var currentTop =
      (window.pageYOffset || doc.scrollTop) - (doc.clientTop || 0);
    if (videoRef.current != null) {
      const { top, height } = videoRef.current.getBoundingClientRect();
      if (Math.abs(top - currentTop) <= height) {
        console.log("play");

        // videoRef.current.play();
      }
    }
  });
  return (
    <section>
      <div className={classes.textGioiThieu}>
        <Image fluid src="/images/gioi_thieu/text_gioi_thieu.png"></Image>
      </div>

      <div className={classes.videoGroup}>
        <div className={classes.video}>
          <Image
            onClick={(e) => {
              e.preventDefault();
            }}
            className={classes.frame + ` pointer-none`}
            fluid
            src="/images/gioi_thieu/frame_2.png"
          ></Image>
          <video
            ref={videoRef}
            className={classes.videoPlay}
            autoPlay={false}
            controls
          >
            <source src="videos/GioiThieu.mp4" type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </div>
        <div
          onClick={(e) => {
            e.preventDefault();
          }}
          className="decor pointer-none"
        >
          <Image
            onClick={(e) => {
              e.preventDefault();
            }}
            fluid
            src="/images/gioi_thieu/decor_1.png"
            style={{
              width: getMobileScaleWidth(145),
              position: "absolute",
              left: 0,
              bottom: "-2%",
            }}
          ></Image>
          <Image
            onClick={(e) => {
              e.preventDefault();
            }}
            fluid
            src="/images/gioi_thieu/decor_2.png"
            style={{
              width: getMobileScaleWidth(118),
              position: "absolute",
              left: "7%",
              bottom: "-12%",
            }}
          ></Image>
          <Image
            onClick={(e) => {
              e.preventDefault();
            }}
            fluid
            src="/images/gioi_thieu/decor_3.png"
            style={{
              width: getMobileScaleWidth(152),
              position: "absolute",
              right: "4%",
              bottom: "-10%",
            }}
          ></Image>
        </div>
      </div>

      <BannerCarousel />
    </section>
  );
};
