import { useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import "./App.css";
import Mobile from "./components/MobileSection";
import { DieuKhoan } from "./pages/DieuKhoan/DieuKhoan";
import { EventsAndNewsPage } from "./pages/EventsAndNews/EventsAndNewsPage";
import { Home } from "./pages/Home/Home";
import * as Helper from "./utils/hepler";

function App() {
  const [isMobile, setIsMobile] = useState(Helper.isMobile());

  window.addEventListener("resize", () => {
    setIsMobile(Helper.isMobile());
  });
  useEffect(() => {
    var hash,
      timeout = 0,
      poll = window.setInterval(function () {
        hash = window.location.hash;

        if (hash.length) {
          document.getElementById(hash.substring(1))?.scrollIntoView({
            behavior: "smooth",
          });
          return window.clearInterval(poll);
        } else if (timeout++ > 100) {
          // cancel the interval after 100 attempts (== 10s)
          return window.clearInterval(poll);
        }
      }, 100);
  }, []);
  return (
    <div className={"App "}>
      {
        <Router>
          <Routes>
            <Route
              exact
              path="/"
              component={isMobile ? <Mobile /> : <Home></Home>}
            />
            <Route
              path="/"
              element={isMobile ? <Mobile /> : <Home></Home>}
            ></Route>
            <Route path="/dieu-khoan" element={<DieuKhoan />}></Route>
            <Route
              path="/su-kien-tin-tuc"
              element={<EventsAndNewsPage />}
            ></Route>
          </Routes>
        </Router>
      }
    </div>
  );
}

export default App;
