import { useState } from "react";
import { Modal } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import Navigator from "../../../utils/navigate-helper";
import { Menu } from "../Menu/Menu";
import { useStyle } from "./style";

export const MobileHeader = () => {
  const classes = useStyle();
  const [openMenu, setOpenMenu] = useState(false);
  const navigate = useNavigate();
  return (
    <header className={classes.header}>
      <div
        onClick={() => {
          navigate("/");
          Navigator.toTop();
        }}
      >
        <img
          className="img-fluid mr-2"
          src="/images/mobile/header/home.png"
          alt="home"
        ></img>
      </div>

      <a className="px-2" href="#events-and-news">
        <img
          className="img-fluid"
          src="/images/mobile/header/text_news.png"
          alt="home"
        ></img>
      </a>

      <div
        className="px-2"
        onClick={() => {
          Navigator.toGame();
        }}
      >
        <img
          className="img-fluid"
          src="/images/mobile/header/button_play.png"
          alt="home"
        ></img>
      </div>

      <div
        onClick={() => {
          setOpenMenu(true);
        }}
      >
        <img
          className="img-fluid button ml-2"
          src="/images/mobile/header/menu_button.png"
          alt="home"
        ></img>
      </div>

      <Modal
        show={openMenu}
        onHide={() => {
          setOpenMenu(false);
        }}
        size="lg"
        centered
        style={{
          backgroundColor: "transparent",
        }}
        className="popup-bg-transparent"
      >
        <Menu />
      </Modal>
    </header>
  );
};
