import { Carousel, CarouselItem, Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { getMobileScaleWidth } from "../../../utils/layout-helper";

const useStyles = createUseStyles({
  banners: {
    position: "absolute",
    top: "21.5%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "80%",
    composes: ["center"],
    "& a": {
      opacity: "1 !important",
    },
  },
  bannerItem: {
    "& img": {
      borderRadius: "16px",
      width: "95%",
    },
    position: "relative",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  bannerFrame: {
    borderRadius: "0",
    position: "absolute",
    width: "98%",
    top: "-2.5%",
    composes: ["center"],
  },
});
export const BannerCarousel = () => {
  const classes = useStyles();

  const imgSrcList = [
    "images/banners/1-800x450.png",
    "images/banners/2-800x450.png",
    "images/banners/3-800x450.png",
    "images/banners/4-800x450.png",
  ];
  return (
    <Carousel
      indicators={false}
      className={classes.banners}
      prevIcon={
        <>
          <Image
            style={{
              marginLeft: "-100%",
              transform: "translateX(-100%)",
              height: getMobileScaleWidth(48),
            }}
            src="/images/gioi_thieu/button_previous.png"
          ></Image>
        </>
      }
      nextIcon={
        <>
          <Image
            style={{
              marginRight: "-100%",
              transform: "translateX(100%)",
              height: getMobileScaleWidth(48),
            }}
            fluid
            src="/images/gioi_thieu/button_next.png"
          ></Image>
        </>
      }
    >
      {imgSrcList.map((img, idx) => {
        return (
          <CarouselItem key={idx}>
            <div className={classes.bannerItem}>
              <Image fluid className={classes.bannerImage} src={img}></Image>
            </div>
            <Image
              onClick={(e) => {
                e.preventDefault();
              }}
              className={classes.bannerFrame + ` pointer-none`}
              fluid
              src="/images/gioi_thieu/frame_2.png"
            ></Image>
          </CarouselItem>
        );
      })}
    </Carousel>
  );
};
