import { Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { CaCarousel } from "./CaCarousel";

const useStyles = createUseStyles({
  heThongCa: {
    position: "absolute",
    top: "40%",
    width: "90%",
    composes: ["center"],
  },
  fishes: {
    position: "absolute",
    top: "48.5%",
    display: "flex",
    alignItems: "center",
    flexDirection: "column",
    composes: ["center"],
    gap: "4rem",
  },
  fish: {
    width: "80%",
    "&:nth-child(n+2)": {
      marginTop: "1%",
    },
  },
  bossFx: {
    position: "absolute",
    top: "60%",
    margin: "0 !important",
    right: "0 !important",
    width: "60%",
    opacity: 0.3,
  },
});
export const FishesSystemSection = () => {
  const classes = useStyles();
  return (
    <section>
      <div className={classes.heThongCa}>
        <Image fluid src="/images/he_thong_ca/text_he_thong_ca.png"></Image>
      </div>
      <div className={classes.fishes}>
        <CaCarousel src="ca_thuong" numFishes={12} />
        <CaCarousel src="ca_dac_biet" numFishes={9} />
        <CaCarousel src="bosses" numFishes={5} />
      </div>

      <Image
        className={classes.bossFx}
        fluid
        src="/images/he_thong_ca/boss_fx.png"
      ></Image>
    </section>
  );
};
