import { Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { getMobileScaleWidth } from "../../../utils/layout-helper";
import { Cannon } from "./Cannon";

const useStyles = createUseStyles({
  heThongSung: {
    position: "absolute",
    top: "67%",
    width: "90%",
    composes: ["center"],
  },
  videoFx2: {
    position: "absolute",
    top: "70%",
    width: "100%",
    mixBlendMode: "screen",
    composes: ["center"],
  },
  videoGroup2: {
    position: "absolute",
    top: "71%",
    width: "100%",
    composes: ["center"],
  },
  videoDecor2: {
    position: "absolute",
    width: "100%",
    top: "72%",
  },
  videoPlay: {
    width: "97%",
    borderRadius: "16px",
  },
  frame: {
    position: "absolute",
    width: "80%",
  },
  video: {
    width: "80%",
    height: "100%",
    margin: "auto",
    overflow: "hidden",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  cannonGroup: {
    position: "absolute",
    top: "76%",
    gap: "2rem",
    width: "90%",
    height: `calc(2 * ${getMobileScaleWidth(309)}px)`,
    composes: ["center"],
  },
  cannon1: {
    position: "absolute",
    left: "5%",
  },
  cannon2: {
    position: "absolute",
    right: "5%",
  },

  cannon3: {
    position: "absolute",
    left: "5%",
    top: "70%",
  },
  cannon4: {
    position: "absolute",
    right: "5%",
    top: "70%",
  },
});

export const CannonsSystem = () => {
  const classes = useStyles();

  return (
    <section>
      <div className={classes.heThongSung}>
        <Image fluid src="/images/he_thong_sung/text_he_thong_sung.png"></Image>
      </div>

      <div className={classes.videoFx2}>
        <Image
          fluid
          className="fx"
          src="/images/he_thong_sung/video_2_decor_fx.png"
        ></Image>
      </div>
      <div className={classes.videoDecor2}>
        <Image fluid src="/images/he_thong_sung/video_2_decor.png"></Image>
      </div>
      <div className={classes.videoGroup2}>
        <div className="videoFrame">
          <div className={classes.video}>
            <Image
              onClick={(e) => {
                e.preventDefault();
              }}
              className={classes.frame + ` pointer-none`}
              fluid
              src="/images/gioi_thieu/frame_2.png"
            ></Image>
            <video className={classes.videoPlay} autoPlay={false} controls>
              <source src="videos/HeThongSung.mp4" type="video/mp4" />
              Your browser does not support the video tag.
            </video>
          </div>
        </div>
      </div>

      <div className={classes.cannonGroup}>
        <div className={classes.cannon1}>
          <Cannon
            cannon="images/he_thong_sung/hoa_tien_cannon.png"
            medal="images/he_thong_sung/medal_2.png"
            title="images/he_thong_sung/hoa_tien_title.png"
            cannonSize={137}
          />
        </div>
        <div className={classes.cannon2}>
          <Cannon
            cannon="images/he_thong_sung/loi_phong_cannon.png"
            medal="images/he_thong_sung/medal_2.png"
            title="images/he_thong_sung/loi_phong_title.png"
            cannonSize={157}
          />
        </div>

        <div className={classes.cannon3}>
          <Cannon
            cannon="images/he_thong_sung/axo_cannon.png"
            medal="images/he_thong_sung/medal_1.png"
            title="images/he_thong_sung/axo_title.png"
            cannonSize={101}
          />
        </div>
        <div className={classes.cannon4}>
          <Cannon
            cannon="images/he_thong_sung/sung_hiem_cannon.png"
            medal="images/he_thong_sung/medal_1.png"
            title="images/he_thong_sung/sung_hiem_title.png"
            cannonSize={139}
          />
        </div>
      </div>
    </section>
  );
};
