import { Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { getMobileScaleWidth } from "../../../utils/layout-helper";

export const Cannon = (props) => {
  const useStyles = createUseStyles({
    canonContainer: {
      position: "relative",
      composes: ["center"],
      width: `${getMobileScaleWidth(288)}px`,
      height: `${getMobileScaleWidth(273 + 58 + 40)}px`,
    },
    bg: {
      mixBlendMode: "screen",
      position: "absolute",
      width: `${getMobileScaleWidth(320)}px`,
      left: "47%",
      transform: "translateX(-50%)",
    },
    medal: {
      position: "absolute",
      left: "-3% !important",
      top: "-2% !important",
      width: "40%",
    },
    cannon: {
      position: "absolute",
      left: "50%",
      width: `${getMobileScaleWidth(props.cannonSize)}px`,
      transform: "translate(-50%, -50%)",
      top: "40%",
    },
    title: {
      position: "absolute",
      bottom: 0,
    },
  });

  const classes = useStyles();
  return (
    <div className={classes.canonContainer}>
      <div className={classes.bg}>
        <Image fluid src="images/he_thong_sung/frame_fx.png"></Image>
      </div>

      <div className={classes.cannon}>
        <Image fluid src={props.cannon}></Image>
      </div>

      <div className={classes.title}>
        <Image fluid src={props.title}></Image>
      </div>
      <div className={classes.medal}>
        <Image fluid src={props.medal}></Image>
      </div>
    </div>
  );
};
