import { Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { MobileLayout } from "../../utils/layout-helper";
import { Logo } from "../Logo/Logo";
import { Features } from "./Features/Features";
import { FishesSystemSection } from "./FishesSystemSection/FishesSystemSection";

import { CannonsSystem } from "./CannonsSytem/CannonsSystem";
import { EventsAndNews } from "./EventsAndNews/EventsAndNews";
import { Introduction } from "./Introduction/Introduction";
import { MobileHeader } from "./MobileHeader/MobileHeader";
import Navigator from "../../utils/navigate-helper";
import { Footer } from "../Footer/Footer";

const useStyles = createUseStyles({
  app: {
    backgroundImage: "url(images/mobile/background.png)",
    height: `${window.innerWidth / MobileLayout.wh_ratio}px`,
    backgroundSize: "100%",
    position: "relative",
  },
  main: {
    marginTop: 65,
  },
  mobileLogo: {
    maxWidth: "33%",
    position: "absolute",

    top: "1.4%",
    composes: ["center"],
  },
  shark: {
    top: "41vw",
    position: "absolute",
    width: `${(651 / MobileLayout.width) * 100}vw`,
    right: "9vw",
  },
  title: {
    position: "absolute",
    top: "8.5%",
    width: `90vw`,
    composes: ["center"],
  },
  titleFx: {
    position: "absolute",
    mixBlendMode: "screen",
    top: "-30%",
    composes: ["center"],
  },
  bubbleFx: {
    position: "absolute",
    top: "5.5%",
    mixBlendMode: "screen",
    composes: ["center"],
  },
  buttonPlay: {
    position: "absolute",
    top: "11.3%",
    width: "60%",
    composes: ["center"],
  },
  lineFx1: {
    position: "absolute",
    top: "13.3%",
    opacity: "0.8",
    width: "90%",
    composes: ["center"],
  },
  lineFx2: {
    position: "absolute",
    top: "86%",
    opacity: "0.8",
    width: "90%",
    composes: ["center"],
  },
  footer: {
    position: "absolute",
    top: "100%",
    width: "100%",
  },
});

function Mobile() {
  const classes = useStyles();
  return (
    <div className={"App " + classes.app}>
      <MobileHeader />
      <div className={classes.main}>
        <section className={classes.section1}>
          <div className={classes.bubbleFx}>
            <Image fluid src="/images/mobile/bubble_fx.png"></Image>
          </div>
          <div className={classes.mobileLogo}>
            <Logo></Logo>
          </div>

          <div className={classes.shark}>
            <Image fluid src="/images/mobile/shark.png"></Image>
          </div>

          <div className={classes.title}>
            <Image
              className={classes.titleFx}
              fluid
              src="/images/title_fx.png"
            ></Image>

            <Image fluid src="/images/title.png"></Image>
          </div>

          <div
            className={classes.buttonPlay}
            onClick={() => {
              Navigator.toGame();
            }}
          >
            <Image fluid src="/images/button_play.png"></Image>
          </div>
        </section>

        <div className={classes.lineFx1}>
          <Image fluid src="/images/line_fx.png"></Image>
        </div>

        <Introduction />

        <Features />

        <FishesSystemSection />

        <CannonsSystem />

        <EventsAndNews />

        <div className={classes.lineFx2}>
          <Image fluid src="/images/line_fx.png"></Image>
        </div>

        <Footer></Footer>
      </div>
    </div>
  );
}

export default Mobile;
