export const GAME_URL =
  "https://zca.zagoo.vn/?utm_source=Website&utm_medium=Website-header&utm_campaign=2010webheader";

export const FACEBOOK_FANPAGE = "https://www.facebook.com/zca.zagoo.vn";

export const FACEBOOK_GROUP = "https://www.facebook.com/groups/615473780289490";

export const SUPPORT = "https://hotro.zagoo.vn/#!/support";

export const ZALO = "https://zalo.me/zcazagoo";

export const FLOATING_PLAY_NOW =
  "https://zca.zagoo.vn/?utm_source=Website&utm_medium=Website-qr&utm_campaign=2510buttonfloating";

export const COMPLAIN = "https://hotro.zagoo.vn/#!/giai_quyet_khieu_nai";
const navigate = (url) => {
  window.location.href = url;
};

const Navigator = {
  toHome: () => {
    navigate("/");
  },
  toGame: () => {
    navigate(GAME_URL);
  },
  toFBFanpage: () => {
    navigate(FACEBOOK_FANPAGE);
  },

  toFBGroup: () => {
    navigate(FACEBOOK_GROUP);
  },

  toSupport: () => {
    navigate(SUPPORT);
  },

  toZalo: () => {
    navigate(ZALO);
  },

  toFloatingPlay: () => {
    navigate(FLOATING_PLAY_NOW);
  },

  scrollToID: (id) => {
    const releventDiv = document.getElementById(id);
    // behavior: "smooth" parameter for smooth movement
    releventDiv.scrollIntoView({ behavior: "smooth" });
  },

  toTop: () => {
    document.body.scrollTop = 0; // For Safari
    document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
  },

  toCompain: () => {
    navigate(COMPLAIN);
  },
};

export default Navigator;
