import { useState } from "react";
import { Image, Modal } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import {
  getMobileScaleWidth
} from "../../../utils/layout-helper";

const useStyles = createUseStyles({
  textTinhNang: {
    position: "absolute",
    top: "26.5%",
    width: "70%",
    composes: ["center"],
  },
  ngocTraiGroup: {
    position: "absolute",
    top: "30.5%",

    "& .fx": {
      position: "absolute",
      mixBlendMode: "screen",
      width: "100%",
      marginTop: "-15%",
      left: 0,
      right: 0,
      margin: "auto",
    },
    "& .ngocTrai": {
      position: "absolute",
      top: "20%",
      width: "62%",
      left: 0,
      right: 0,
      margin: "auto",
    },
    composes: ["center"],
  },
  features: {
    position: "absolute",
    top: "28.5%",
    height: `110vw`,
    width: "100%",
    "& div": {
      position: "absolute",
      width: "90%",
    },
    "& .item": {
      position: "absolute",
    },
    composes: ["center"],
  },
  bubble: {
    position: "absolute",
    width: "78%",
    top: "30.2%",
    composes: ["center"],
  },
});

export const Features = () => {
  const classes = useStyles();

  const [openItem, setOpenItem] = useState();

  const features = [
    {
      src: "/images/tinh_nang/bomb1.png",
      height: 172,
      bottom: "-1%",
      right: "25.5%",
      item: "/images/tinh_nang/items/bomb1.png",
    },
    {
      src: "/images/tinh_nang/bomb2.png",
      height: 163,
      bottom: "6%",
      left: "20%",
      item: "/images/tinh_nang/items/bomb2.png",
    },
    {
      src: "/images/tinh_nang/da_nang_cap.png",
      height: 168,
      top: "27%",
      left: "1.5%",

      item: "/images/tinh_nang/items/da_nang_cap.png",
    },
    {
      src: "/images/tinh_nang/khoa_muc_tieu.png",
      height: 147,
      top: "10%",
      right: "14%",
      item: "/images/tinh_nang/items/khoa_muc_tieu.png",
    },
    {
      src: "/images/tinh_nang/the_cuong_no.png",
      height: 173,
      top: "44%",
      right: "0%",
      item: "/images/tinh_nang/items/the_cuong_no.png",
    },
    {
      src: "/images/tinh_nang/the_dong_bang.png",
      height: 167,
      top: "2%",
      left: "23%",
      item: "/images/tinh_nang/items/the_dong_bang.png",
    },
  ];

  return (
    <section>
      <div className={classes.textTinhNang}>
        <Image fluid src="/images/tinh_nang/text_tinh_nang.png"></Image>
      </div>
      <div className={classes.ngocTraiGroup}>
        <div className="fx">
          <Image fluid src="/images/tinh_nang/ngoc_trai_fx.png"></Image>
        </div>
        <div className="ngocTrai">
          <Image fluid src="/images/tinh_nang/ngoc_trai.png"></Image>
        </div>
      </div>

      <Image
        className={classes.bubble}
        fluid
        src="/images/tinh_nang/bubble_fx.png"
      />
      <div className={classes.features}>
        {/* <Image fluid src="/images/tinh_nang/features.png"></Image> */}

        {features.map((feature, idx) => {
          return (
            <Image
              onClick={() => {
                setOpenItem(idx);
              }}
              className="item"
              key={idx}
              fluid
              src={feature.src}
              style={{
                height: getMobileScaleWidth(feature.height),
                top: feature.top,
                left: feature.left,
                bottom: feature.bottom,
                right: feature.right,
              }}
            />
          );
        })}
      </div>

      <Modal
        centered
        className="popup-bg-transparent"
        size="lg"
        show={openItem != null}
        onHide={() => setOpenItem(undefined)}
      >
        <div
          style={{
            width: "80%",
            margin: "auto",
            textAlign: "center",
          }}
        >
          <Image className="w-100" fluid src={features[openItem]?.item} />
        </div>
      </Modal>
    </section>
  );
};
