export const MobileLayout = {
  width: 768,
  height: 8559,
  wh_ratio: 768 / 8559,
};

export const WebsiteLayout = {
  height: 6705,
  width: 1920,
};

export const getMobileScaleWidth = (width) => {
  return (window.innerWidth / MobileLayout.width) * width;
};

export const getScale = () => {
  return window.innerWidth / MobileLayout.width;
};

export const LayoutHelper = {
  getWebsiteScaleWidth: (width) => {
    return (window.innerWidth / WebsiteLayout.width) * width;
  },
  getWebsiteRatio: (width) => {
    return (width / WebsiteLayout.width) * 100;
  },
  getWebsiteHeightRatio: (height) => {
    return (height / WebsiteLayout.height) * 100;
  },
  getPCRatioWidth: (width) => {
    return MobileLayout.wh_ratio * width;
  },
  MobileLayout,
  WebsiteLayout,
};
