import { Carousel, CarouselItem, Image, Stack } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { BannerItem } from "./BannerItem";

const useStyles = createUseStyles({
  banners: {
    position: "absolute",
    top: "30.5%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "55%",
    composes: ["center"],
    "& a": {
      opacity: "1 !important",
    },
  },
  prev: {
    marginLeft: "-50% !important",
    transform: "translateX(-100%)",
    height: 48,
  },
  next: {
    marginRight: "-50%",
    transform: "translateX(100%)",
    height: 48,
  },
  // "@media (min-width: 1900px)": {
  //   banners: {
  //     top: "30.3%",
  //     width: "55%",
  //   },
  //   prev: {
  //     marginLeft: "-100%",
  //     transform: "translateX(-100%)",
  //     height: 48,
  //   },
  //   next: {
  //     marginRight: "-100%",
  //     transform: "translateX(100%)",
  //     height: 48,
  //   },
  // },
  // "@media (min-width: 1400px) and (max-width: 1899px)": {
  //   prev: {
  //     marginLeft: "-100%",
  //     transform: "translateX(-100%)",
  //     height: 48,
  //   },
  //   next: {
  //     marginRight: "-100%",
  //     transform: "translateX(100%)",
  //     height: 48,
  //   },
  // },
});
export const BannerCarousel = () => {
  const classes = useStyles();

  const imgSrcList = [
    "images/banners/1-800x450.png",
    "images/banners/2-800x450.png",
    "images/banners/3-800x450.png",
    "images/banners/4-800x450.png",
  ];
  return (
    <Carousel
      indicators={false}
      touch={true}
      className={classes.banners}
      prevIcon={
        <Image
          classname={classes.prev}
          style={{
            marginLeft: "-80%",
            transform: "translateX(-100%)",
            height: 48,
          }}
          src="/images/gioi_thieu/button_previous.png"
        ></Image>
      }
      nextIcon={
        <>
          <Image
            style={{
              marginRight: "-80%",
              transform: "translateX(100%)",
              height: 48,
            }}
            src="/images/gioi_thieu/button_next.png"
          ></Image>
        </>
      }
    >
      {imgSrcList.map((img, idx) => {
        const imgs = [
          img,
          imgSrcList[(idx + 1) % imgSrcList.length],
          imgSrcList[(idx + 2) % imgSrcList.length],
        ];
        return (
          <CarouselItem key={idx}>
            <Stack
              direction="horizontal"
              className="h-100 justify-content-center align-items-center"
            >
              <BannerItem src={imgs[0]} />
              <BannerItem src={imgs[1]} />
              <BannerItem src={imgs[2]} />
            </Stack>
          </CarouselItem>
        );
      })}
    </Carousel>
  );
};
