import { Image } from "react-bootstrap";
import { createUseStyles } from "react-jss";
import { useNavigate } from "react-router-dom";
import Navigator from "../../../utils/navigate-helper";

const useStyles = createUseStyles({
  title: {
    position: "absolute",
    composes: ["center"],
    top: "87%",
    width: "90%",
  },
  buttonGroup: {
    position: "absolute",
    composes: ["center"],
    top: "89%",
    width: "90%",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  banner: {
    position: "absolute",
    top: "93%",
  },
});

export const EventsAndNews = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  return (
    <section>
      <div id="events-and-news" className={classes.title}>
        <Image fluid src="images/su_kien_tin_tuc/title.png" />
      </div>

      <div className={classes.buttonGroup}>
        <div>
          <Image fluid src="images/su_kien_tin_tuc/su_kien.png" />
        </div>
        <div>
          <Image fluid src="images/su_kien_tin_tuc/tin_tuc.png" />
        </div>
      </div>

      <div
        className={classes.banner}
        onClick={() => {
          navigate("/su-kien-tin-tuc");
          Navigator.toTop();
        }}
      >
        <Image fluid src="images/su_kien_tin_tuc/banner.png" />
      </div>
    </section>
  );
};
