import { createUseStyles } from "react-jss";

export const useStyle = createUseStyles({
  header: {
    top: 0,
    position: "fixed",
    display: "flex",
    justifyContent: "space-between",
    backgroundImage: "url(images/mobile/header/header.png)",
    alignItems: "center",
    maxHeight: "100px",
    width: "100%",
    height: "auto",
    padding: "1rem 0",
    "& img": {
      height: "100%",
    },

    "& img.button": {
      maxHeight: "100%",

      padding: "0 10%",
    },
    "& div": {
      maxHeight: "100%",
      margin: "auto",
    },
    zIndex: 99,
  },
});
